.playing .anim-border {
  position: absolute;
  width: 100%;
  height: 100%;
}
.playing .anim-line {
  height: 1px;
  width: 100%;
  background-color: red;
  position: absolute;
  transition: all 1.5s;
}
.playing .anim-line:nth-child(1) {
  top: 30px;
  transition-delay: 500ms;
  margin: 0 auto;
  left: 0;
  right: 0;
  width: calc(100% - 2*30px);
  animation: width-anim 1.5s linear 500ms 1;
  animation-fill-mode: forwards;
}
.playing .anim-line:nth-child(2),
.playing .anim-line:nth-child(3) {
  left: 30px;
  height: 100%;
  width: 1px;
  margin: auto 0;
  height: 0;
  top: 0;
  bottom: 0;
  animation: height-anim 1.5s linear 500ms 1;
  animation-fill-mode: forwards;
}
.playing .anim-line:nth-child(3) {
  right: 30px;
  left: initial;
}
.playing .anim-line:nth-child(4) {
  bottom: 30px;
  width: 0;
  right: calc(60% - 30px);
  animation: width-anim-half 1.5s linear 500ms 1;
  animation-fill-mode: forwards;
  opacity: 1;
}
@media screen and (max-width: 767px) {
  .playing .anim-line:nth-child(4) {
    animation: width-anim-mobile 1.5s linear 500ms 1 forwards;
    right: calc(80% - 30px);
  }
}
.playing .anim-line:nth-child(5) {
  bottom: 30px;
  left: calc(60% - 30px);
  width: 0;
  animation: width-anim-half 1.5s linear 500ms 1;
  animation-fill-mode: forwards;
}
@media screen and (max-width: 767px) {
  .playing .anim-line:nth-child(5) {
    animation: width-anim-mobile 1.5s linear 500ms 1 forwards;
    left: calc(80% - 30px);
  }
}
.playing .more {
  opacity: 0;
  animation-fill-mode: forwards;
  animation-duration: 500ms;
  animation-name: opacity;
  animation-iteration-count: 1;
  animation-delay: 2750ms;
}
.playing .anim-arrow {
  position: absolute;
  top: 60%;
  height: 30%;
  left: 50%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
@media screen and (max-width: 767px) {
  .playing .anim-arrow {
    top: 50%;
  }
}
.playing .anim-arrow .arrow {
  bottom: 0;
  margin-left: -5px;
  opacity: 0;
  animation-fill-mode: forwards;
  animation-duration: 500ms;
  animation-name: opacity;
  animation-iteration-count: 1;
  animation-delay: 2500ms;
}
.playing .anim-arrow .circle {
  width: 4px;
  height: 4px;
  background-color: #fff;
  border-radius: 50%;
  opacity: 0;
  animation-fill-mode: forwards;
  animation-duration: 500ms;
  animation-name: opacity;
  animation-iteration-count: 1;
  margin-left: -2px;
}
.playing .anim-arrow .circle:nth-child(1) {
  animation-delay: 500ms;
}
.playing .anim-arrow .circle:nth-child(2) {
  animation-delay: 750ms;
}
.playing .anim-arrow .circle:nth-child(3) {
  animation-delay: 1000ms;
}
.playing .anim-arrow .circle:nth-child(4) {
  animation-delay: 1250ms;
}
.playing .anim-arrow .circle:nth-child(5) {
  animation-delay: 1500ms;
}
.playing .anim-arrow .circle:nth-child(6) {
  animation-delay: 1750ms;
}
.playing .anim-arrow .circle:nth-child(7) {
  animation-delay: 2000ms;
}
.playing .anim-arrow .circle:nth-child(8) {
  animation-delay: 2250ms;
}
.playing .lupe {
  transition: all 250ms;
  cursor: pointer;
  opacity: 0;
  z-index: -1;
  margin-top: -100px;
  transition-delay: 0ms;
}
.playing .lupe:hover {
  transform: scale(1.3);
}
.playing .lupe.active {
  filter: grayscale(100%) brightness(140%);
  transform: scale(1);
  cursor: initial;
  transition-delay: 0ms;
}
.playing .slide-from-down {
  opacity: 0;
  transform: translateY(30px);
  transition: all 1s;
  transition-delay: 500ms;
}
.playing .slide-from-down.active {
  opacity: 1;
  transform: translateY(0px);
}
.playing .transition-delay10 {
  transition-delay: 1000ms;
}
.playing .transition-delay15 {
  transition-delay: 1500ms;
}
.playing .preloader {
  display: inline-block;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 15px solid red;
  position: relative;
  box-sizing: border-box;
  animation: rotate 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
}
.playing .preloader .bar {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #fff;
  width: 100px;
  height: 20px;
  margin-left: -15px;
}
.playing .preloader .bar:first-child {
  height: 100px;
  width: 20px;
  margin-top: -15px;
  margin-left: auto;
}
.playing .anim-arrow .arrow.jumping {
  animation: jumping 1s cubic-bezier(1, 0.17, 1, 1) 0s infinite alternate;
  opacity: 1;
}
@keyframes jumping {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(25px);
  }
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(90deg);
  }
}
@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes width-anim {
  from {
    width: 0%;
  }
  to {
    width: calc(100% - 2*30px);
  }
}
@keyframes width-anim-half {
  from {
    width: 0%;
  }
  to {
    width: 40%;
  }
}
@keyframes width-anim-mobile {
  from {
    width: 0px;
  }
  to {
    width: 20%;
  }
}
@keyframes height-anim {
  from {
    height: 0%;
  }
  to {
    height: calc(100% - 2*30px);
  }
}
